import React from 'react'
import "./typesbtn.css"
import { Link } from "react-router-dom";

export default  function Typesbtn(props) {
    let statee;

    if((props.levelneed))
    {
        statee="active";
        return (
            <Link to={props.link} className={statee}>
            <div className="type">
                <img src={props.icon} alt={props.title} />
                <div className="text">
                    <h2>{props.title}</h2>
                    <p>{props.dis}</p>
                </div>
            </div>
            </Link>
        )
    }
    else
    {
        statee="notactive";
        return (
            <Link to="/" className={statee}>
            <div className="type">
                <img src={props.icon} alt={props.title} />
                <div className="text">
                    <h2>{props.title}</h2>
                    <p>{props.dis}</p>
                </div>
                <div className="msg">
                    {props.levelmsg}
                </div>
            </div>
            </Link>
        )
    }
    
}
